@import '../../main.scss';

.wrapper {
    display: flex;
    height: 100%;
}

.filtersContainer {
    min-height: 100vh;
    border-right: 1px solid $grey;
    padding-top: $headerHeight;
    user-select: none;
}

.top {
    padding-right: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 50px;
    border-bottom: 1px solid $grey;
    cursor: pointer;
    padding-left: $desctop1Indentation;
    
    @include desktop1440 {
        padding-left: $desctop2Indentation;
    }
}

.title {
    font-size: $preMiddleFontSize;
    font-weight: 500;
}

.titleArrow {
    transform: rotate(90deg);
}

.filters {
    padding-right: 20px;
    padding-left: $desctop1Indentation;
    
    @include desktop1440 {
        padding-left: $desctop2Indentation;
    }
}

.itemsContainer {
    // height: 100vh;
    flex: 1;
    padding-top: $headerHeight;
}

.criteria {
    margin-top: 30px;
    padding-left: 30px;

    border-bottom: 1px solid $grey;
    padding-right: $desctop1Indentation;
    
    @include desktop1440 {
        padding-right: $desctop2Indentation;
    }
}

.search {
    height: 40px;
    width: 100%;
    background-color: $white;
    border-radius: 20px;
    margin-bottom: 20px;
    display: flex;
    align-items: center;
    padding: 0 15px;
    &:hover {
        box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
    }
}

.input {
    font-size: $commonFontSize;
    width: 100%;
    &::placeholder {
        color: $darkGrey;
    }
}

.sort {
    margin-bottom: 10px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
}

.sortTitle {
    font-size: $commonFontSize;
    color: $darkGrey;
    margin-bottom: 10px;
}

.list {
    display: grid;
    grid-template-columns: [col-start] repeat(4, 1fr) [col-end];
    gap: 20px;
    margin-top: 30px;
    margin-left: 30px;
    padding-right: $desctop1Indentation;
    
    @include desktop1440 {
        padding-right: $desctop2Indentation;
        grid-template-columns: [col-start] repeat(3, 1fr) [col-end];

    }

    @include desktop960 {
        grid-template-columns: [col-start] repeat(2, 1fr) [col-end];
    }

    @include tablets760 {
        grid-template-columns: [col-start] repeat(1, 1fr) [col-end];
    }
}