@import "../../main.scss";


.wrapper {
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    padding: 0 20px;
    background-color: $grey;
    border-radius: 20px;
    min-width: 260px;
}

.top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 40px;
    cursor: pointer;
}

.title {
    color: $darkGrey;
    font-size: $commonFontSize;
    font-weight: 500;
}

.arrow {

    transition: transform .5s;

    &.active {
        transform: rotate(-180deg);
    }
}

.container {
    height: 0;
    transition: height .5s;
    overflow: hidden;
}

.block {
    padding-top: 10px;
    padding-bottom: 20px;
}