@import "../../main.scss";

.wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 50px;
    width: 50px;
    border-radius: 25px;
    cursor: pointer;

    &.active {
        border: 2px solid $dominant;
    }

}

.container {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 40px;
    width: 40px;
    border-radius: 20px;
    background-color: $grey;
    overflow: hidden;
    border: 1px solid $darkGrey;
}

.icon {
    padding-top: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
}