@import "../../main.scss";


.wrapper {
    position: relative;
    margin-right: 10px;
    cursor: pointer;

    &:last-child {
        margin-right: 0;
    }
}

.alert {
    position: absolute;
    display: none;
    justify-content: center;
    align-items: center;
    text-align: center;
    height: 20px;
    width: 20px;
    border-radius: 10px;
    background-color: $dominant;
    z-index: 100;
    right: -3px;
    top: -1px;
    color: $white;
    font-weight: 700;
    font-family: Calibri;
    font-size: 12px;
    border: 2px solid $lightGray;
}

.active {
    display: flex
}


.container {
    position: relative;
    height: 40px;
    width: 40px;
    border-radius: 20px;
    background-color: $lightGray;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    transition: background-color .5s;
}

.icon {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}