@import "../../main.scss";


.wrapper {
    position: relative;
    width: 100%;
}

.section {
    position: relative;
    height: 100vh;
    overflow: hidden;
}

.treeBackground {
    position: absolute;
    top: -30vh;
    left: 50vw;
    z-index: -10;
}

.container {
    padding-top: $headerHeight;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
}

.description {
    margin-top: 15vh;
    color: $graphite;
    margin-left: $desctop1Indentation;
    margin-right: $desctop1Indentation;
    font-family: Calibri, sans-serif;

    
    @include desktop1440 {
        margin-top: 10vh;
        margin-left: $desctop2Indentation;
        margin-right: $desctop2Indentation;
    }
}

.title {
    font-size: 64px;
}

.title2 {
    font-size: 64px;
    color: $dominant;
}

.descriptionText {
    font-size: 24px;
    font-weight: 400;
    font-weight: 700;
}
