@import '../../../main.scss';

.slider {
    padding: 0 10px;
}

.range {
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: space-between;
    margin-top: 5px;
    font-family: Calibri, sans-serif;
}

.from, .to {
    height: 20px;
    width: 100px;
    background-color: $white;
    border-radius: 10px;
    display: flex;
    align-items: center;
    padding: 0 10px;
}

.between {
    font-size: 20px;
    font-weight: 700;
    color: $darkGrey;
}

.rangeTitle {
    color: $darkGrey;
}

.rangeInput {
    margin-left: 5px;
    width: 60px;
    color: $graphite;
}