@import "../../main.scss";

.wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: $white;
    user-select: none;
    border-radius: 20px;
    padding: 20px;
    min-width: 220px;
    color: $graphite;
}

.top {
    position: relative;
    width: 100%;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
}

.status {
    color: $darkGrey;
    padding-left: 16px;
    display: flex;
    align-items: center;

    &::before {
        content: '';
        position: absolute;
        left: 0;
        height: 10px;
        width: 10px;
        background-color: $darkGrey;
        border-radius: 5px;
        margin-bottom: 2px;
    }

    &.active {
        color: $dominant;

        &::before {
            background-color: $dominant;
        }
    }
}

.favorites {
    cursor: pointer;
}

.image {
    cursor: pointer;
    width: 100%;
    display: flex;
    justify-content: center;
}

.description {
    width: 100%;
}

.name {
    font-size: 20px;
    font-weight: 700;
    margin-bottom: 6px;
    cursor: pointer;
}

.rating {
    display: flex;
    margin-bottom: 6px;
}

.ratingItem {
    display: flex;
    align-items: center;

    &:first-child {
        margin-right: 10px;
    }
}

.estimation {
    margin-left: 5px;
    font-family: Calibri, sans-serif;
}

.reviewers {
    margin-left: 5px;
    font-family: Calibri, sans-serif;
}

.price {
    font-size: 20px;
    font-weight: 700;
    margin-bottom: 20px;
    font-family: Calibri, sans-serif;
}

.order {
    display: flex;
    justify-content: space-between;
}

.count {
    display: flex;
    height: 40px;
    align-items: center;
    background-color: $grey;
    border-radius: 20px;
    min-width: 90px;
    justify-content: space-between;
    font-weight: 700;
    font-family: Calibri, sans-serif;
}

.go {
    padding: 0 10px;
    font-size: 20px;
    padding-bottom: 5px;
    cursor: pointer;
}

.quantity {
    width: 20px;
    text-align: center;
}

.add {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 20px;
    height: 40px;
    width: 90px;
    background-color: $dominant;
    font-weight: 700;
    color: $white;
    cursor: pointer;
    &:hover {
        box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.3);
    }

    &.active {
        border: 1px solid $dominant;
        background-color: $white;
        color: $dominant;
    }
}