@import '../../main.scss';

.wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: $grey;
    padding: 0 15px;
    height: 30px;
    border-radius: 15px;
    margin-left: 20px;
    margin-bottom: 10px;
    user-select: none;
    cursor: pointer;

    &:hover {
        box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
    }
}

.title {
    font-size: 20px;
    color: $darkGrey;
    margin-right: 10px;
}

.arrow {
    transition: transform .5s;

    &.up {
        transform: rotate(-180deg);
    }
}